.movie_container{
    align-items: center;
    justify-content: center;
    display: flex;
    height: 92.4vh;
    /* background-image: url('https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcTezW_A8xzxzcmWE5BPVA6tjrmxh4lQtPz97Q&usqp=CAU'); */
    background-repeat: no-repeat;
    object-fit: contain;
    background: linear-gradient(#ebd4d4, #e8e9f5);
}
.movie_data input{
    font-weight: 700;
    font-size: 14px;
}
.movie_data{
    width: 80%;
    height: 480px;
    /* background-color: linear-gradient(to right, #3ab2dc, #12c7d5, #4dd8c0, #8ee5a3, #cfec8c); */
    background-color: rgb(117, 175, 237);
    border-radius: 10px;
    
}
.left-section{
    margin-left: 40px;
    width: 45%;
    height: 350px;
    float: left;
    margin-top: 40px;

}
.right_section{
    float: right;
    width: 45%;
    height: 350px;
    margin-top: 40px;
